				"html": "<p>Last week was the 3rd week of March, 2017. I was able to have some fun surfing later in the week. I continued to progress in my transfer from 1 team to another at work. Also, my car was side-swiped while I was at Dinner in Venice. All in all, it was a fair week‚Äîconsidering the car side-swiping.</p>\n<h2>Changing Teams</h2>\n<p>Changing teams is tough but great for challenging new skills and learning. The product that I was working on before (a <a href=\"https://content.dollarshaveclub.com/\">content</a> site) still exists leading to fair amount of continued conversation on that product. Meanwhile, I have a whole new stack, (an analytics tool) that I‚Äôm working on now. The really cool part of this setup is that I get to work on a lot of things. The not as cool part [for right now], is the amount of different things I‚Äôm working on is almost too much to take on all at once. It‚Äôs a lot for my brain to consume and comprehend when there is context switching between old progress, new team dynamics, new tools and new skills that I need to learn. In this case, ‚Äòcontext switching‚Äô means changing up a thought process and/or workflow a lot. </p>\n<p>As far as what I‚Äôm learning, I‚Äôm getting much more comfortable with Docker. I‚Äôm also getting a crash course in debugging. The best thing I‚Äôve done so far is destroy my local dev box version needlessly a few times and disconnect my local MySQL for the content site which does not run on docker. It hasn‚Äôt been the most fun but I‚Äôve gained a lot from debugging and I‚Äôve been able to work with more developers‚Äîvery cool.</p>\n<h2>Surfing Time</h2>\n<p>Late the week before last, I was getting out of the water and over extended my leg a bit so I wasn‚Äôt able to surf. This was good for focusing my time on learning my new workflow at work but still a bummer. </p>\n<p>Then the weekend came and my body was feeling pretty good. I added fins and a leash to my new fun board‚Äîa 7‚Äô2‚Äù for small wave days. The new board took some getting used to but after a few perls, I was able to get some fun long rides in on Saturday and Sunday. </p>\n<figure>\n  <img src=\"//yowainwright.imgix.net/wk-11/jetty-1.jpg?w=800&h=800&crop=focalpoint&auto=format\" alt=\"Jetty in Playa Del Ray\" />\n</figure>\n<h2>Getting Side-swiped</h2>\n<p>I‚Äôve often heard that part of being an Angeleno is dealing with what happens when your car incurs damage when parked. Well, if that is the case then I‚Äôm officially an Angeleno. I went to dinner on Sunday and came back to tripping on something while getting ready to get into my car. The thing that I tripped on was my side view mirror. Upon closer inspection of my car in the darkness (it was night), my side view mirror was off and it had scratches from my front bumper, on both side doors, and my rear bumper. After going to the body shop I learned it was well over a $1k in damages. The person that ran into my car left no note so 100% of the cost was on me‚Äîor so I thought. </p>\n<p>After going to my local body shop, they informed me that I could contact my insurance without filing a claim if I stated, ‚ÄòI didn‚Äôt want to file a claim‚Äô when the conversation started. So I did that and found the insurance company to be very helpful. After discussing my car issue with my insurance, I found I would only have to pay the co-pay of my damages plus my insurance would not go up! </p>\n<p>All-in-all, I learned new things from side-swipe situation. It is going to cost me about $600 after it‚Äôs all said and done‚ÄîWAY better than what I thought.</p>\n<h2>Concluding the week</h2>\n<p>This week will go down as fair because, ‚Äòdarn-it‚Äô‚Äîbeing injured and having my car side-swiped was not fun. However, on the level of learning, it was a big week for me! üôÉ</p>",
